* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: aliceblue;
    color: #222;
} 

.booklist {
    width: 90vw;
    max-width: 1170px;
    margin: 5rem auto;
    display: grid;
    gap: 2rem;
    flex-grow: 10;
}


.book {
    background-color: #fff;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    position: relative;
    border: 1px solid black;
}

.book img{
    width: 100%;
    object-fit: cover;
}

.book h2 {
    margin-top: 1rem;
    font-size: 1rem;
}

.book h4 {
    color: #617d98;
    font-size: 0.75rem;
    margin-top: 0.54rem;
}

.book p {
    font-size: 2rem;
    top: 0;
    background-color: goldenrod;
    width: 20%;
    border-top-right-radius: 20px;
    margin-bottom: 20px;
    color: white;
}

h1 {
    text-align: center;
    margin-top: 50px;
}

@media screen and (min-width: 768px) {
    .booklist {
        grid-template-columns: repeat(3, 1fr );
        align-items: start;
    }
}